import { IncomingMessage } from 'http';
import { Connection } from 'rethinkdb';

import { SessionId, Uuid } from 'types';

import { ActualTables } from './types/ActualTables';

export type GetUserIdProps =
	| {
			sessionid: SessionId;
	  }
	| { token: SessionId; botId: Uuid };
export const getUserId = async (
	{
		headers: { sessionid, botid, token },
	}: IncomingMessage | { headers: Record<string, string> },
	tables: ActualTables,
	connection: Connection
): Promise<string> => {
	let query;
	if (sessionid) {
		const session = tables.Sessions.get(sessionid);
		query = session.branch(session('userId'), { error: 'Invalid session' });
	} else {
		const user = tables.Users.get(botid);
		query = user.branch(
			user('botToken')
				.default(null)
				.eq(token)
				.branch(user('id'), { error: 'Invalid bot token' }),
			{ error: 'Invalid bot ID' }
		);
	}
	const res = await query.run(connection);
	if (typeof res !== 'string') throw new Error('498');
	return res;
};
