import en_AR from 'strings/en-AR.json';
import en_US_oxendict from 'strings/en-GB-oxendict.json';
import en_US from 'strings/en-US.json';
import ru_RU from 'strings/ru-RU.json';
import { writeFileSync } from 'typedoc/dist/lib/utils';

type EnUs = typeof en_US;

export const phrasebooks = {
	'en-US': en_US,
	'ru-RU': ru_RU,
	'en-AU': en_AR,
	'en-GB-oxendict': en_US_oxendict,
} as const;
export type Phrasebooks = typeof phrasebooks;
export type Lang = keyof Phrasebooks;
export type Phrasebook = (typeof phrasebooks)[keyof typeof phrasebooks];
export type CompactedPhrasebook = Record<
	keyof Phrasebook,
	Phrasebook[keyof Phrasebook]
> &
	EnUs;
//console.log('ENUS', en_US);
const defaultPhrases = Object.entries(en_US) as [
	keyof EnUs,
	EnUs[keyof EnUs]
][];
const compactPhrasebook = (
	lang: Lang,
	book: Phrasebook
): [Lang, CompactedPhrasebook] => {
	if (lang === 'en-US') return [lang, en_US];
	defaultPhrases.forEach(([name, phrase]) => {
		if (!(name in book)) Object.assign(book, { name: phrase });
	});
	return [lang, book] as [Lang, CompactedPhrasebook];
};
const langList = Object.entries(phrasebooks).map(([l, b]) =>
	compactPhrasebook(l as Lang, b)
);
export const compactedPhrasebooks = Object.fromEntries(langList);

export const genericPhrasebooks: Record<string, CompactedPhrasebook> = {};

for (const [lang, phrasebook] of langList) {
	for (
		let prefix: string | undefined = lang;
		prefix;
		prefix = prefix.match(/^(.+)-/)?.[1]
	) {
		if (!genericPhrasebooks[prefix])
			genericPhrasebooks[prefix] = phrasebook;
	}
}
export const getPhrasebook = (lang: string): CompactedPhrasebook | undefined =>
	lang in phrasebooks
		? compactedPhrasebooks[lang as Lang]
		: genericPhrasebooks[lang.substring(0, 2)] ?? en_US;
