import { group } from '../../group';
import { groupName } from '../../groupName';
import { groupSlug } from '../../groupSlug';
import { boolean } from '../../primitives';
import { uuid } from '../../uuid';

export const updateGroup = {
	request: {
		type: 'object',
		properties: {
			id: uuid,
			name: groupName,
			slug: groupSlug,
			private: boolean,
		},
		minProperties: 2,
		required: ['id'],
	},
	response: {
		type: 'object',
		properties: {
			group,
		},
		required: ['group'],
	},
	authenticated: true,
} as const;
