import { group } from '../../group';
import { groupMembership } from '../../groupMembership';

const groupFilter = {
	enum: ['iCreated', 'iOwn', 'imIn'],
};
export const listGroups = {
	request: {
		type: 'object',
		properties: {
			filters: {
				type: 'array',
				items: groupFilter,
			},
		},
		required: [],
	},

	response: {
		type: 'object',
		properties: {
			groups: {
				type: 'array',
				items: group,
			},
			memberships: {
				type: 'array',
				items: groupMembership,
			},
		},
		required: ['groups'],
	},
	authenticated: false,
} as const;
