import { IncomingMessage } from 'http';
import { Connection, Document } from 'rethinkdb';
import { now } from 'rethinkdb';

import { Session, SessionId, UserLogin } from 'types';

import { generateSessionId } from './generateSessionId';
import { ActualTables } from './types/ActualTables';

export const createSessionForUser = async (
	userId: string,
	msg: IncomingMessage,
	tables: ActualTables,
	connection: Connection
): Promise<SessionId> => {
	const id = generateSessionId();
	const userLogin: Document<Omit<UserLogin, 'id'>> = {
		created: now(),
		ip:
			(msg.headers['CF-Connecting-IP'] as string) ??
			msg.headers.location ??
			'',
		userId,
	};
	const session: Document<Session> = {
		id,
		userId,
		expiration: now().add(60 * 60 * 24 * 30),
	};
	await Promise.all([
		tables.Logins.insert(userLogin).run(connection),
		tables.Sessions.insert(session).run(connection),
	]);
	return id;
};
