import { Document, branch, now, uuid } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { Channel, Group, SanitizedChannel } from 'types';

import { useContract } from '../Contract';
import { CreateStreamResponse } from '../CreateStreamResponse';
import { Route } from '../Route';
import { cfAccountId, cfApiToken } from '../env';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

import phin = require('phin');

export const createGroup = useContract(
	'createGroup',
	async (
		{ name, slug, private: p },
		{ tables, connection },
		msg,
		strings
	) => {
		if (!tables) {
			console.error('Tables missing');
			throw new Error(strings.unknownBackendError);
		}
		const lowerSlug = slug.toLowerCase();
		const userId = await getUserId(msg, tables, connection);
		const user = tables.Users.get(userId);
		const groups = tables.Groups.getAll(userId, {
			index: 'owner',
		});
		const underLimit = groups.count().lt(user('groupLimit').default(50));
		const groupExists = tables.Groups.filter((group) =>
			group('slug').downcase()
		)
			.limit(1)
			.count()
			.gt(0);
		const query = branch(
			groupExists,
			{ error: strings.groupAlreadyExists },
			branch(
				underLimit,
				{ uuid: uuid() },
				{ error: strings.groupLimitReached }
			)
		);
		const res = await query.run(connection);
		if (!res) {
			console.error('idk wtf happened but res is undefined');
			throw new Error(strings.unknownBackendError);
		}
		if ('error' in res) {
			throw new Error(res.error);
		}
		console.log(
			'User exists and is below group limit. Query returned',
			res
		);
		const { uuid: id } = res;

		const group: Document<Group> = {
			id,
			owner: userId,
			created: now(),
			name,
			slug: name.toLowerCase(),
			private: p,
		};
		const insertResult = await tables.Groups.insert(group).run(connection);
		const membership = await tables.GroupMemberships.insert(
			{
				groupId: id,
				userId,
				id: `${userId}~${id}`,
			},
			{ returnChanges: true }
		)('inserted').run(connection);
		console.log('Inserted group', id, 'in database', insertResult);
		return { group, membership };
	}
);
