import { Document } from 'rethinkdb';

import { Channel, SanitizedChannel } from 'types';

// type ObjFunc<T> = (k: keyof T) => T[typeof k] extends Record<string, unknown> ? ObjFunc<T[typeof k]> : T[typeof k]
// type ChannelFunc = ObjFunc<Channel>;
export const sanitizeChannelRecord = (
	channel: Row<Channel>
): Document<SanitizedChannel> => ({
	live: channel('live'),
	id: channel('id'),
	name: channel('name'),
	slug: channel('slug'),
	owner: channel('owner'),
	logo: channel('logo').default(''),
	whepKey: channel('cf')('whepKey').default(null),
	tagline: channel('tagline').default(''),
	bio: channel('bio').default(''),
	activeBg: channel('activeBg').default(null),
	awayBg: channel('awayBg').default(null),
});
