import { email } from './email';
import { gameStats } from './gameStats';
import { hex } from './hex';
import { boolean, string, whole } from './primitives';
import { sessionId } from './sessionId';
import { username } from './username';
import { uuid } from './uuid';

export const special = {
	enum: ['cowboy', 'sombrero'],
} as const;

export const baseUser = {
	type: 'object',
	properties: {
		banned: boolean,
		confirmed: boolean,
		bot: {
			...boolean,
			description: 'Whether or not this user is a bot',
		},
		email: {
			...email,
			description: 'Email used for password resets',
		},
		groupLimit: {
			...whole,
			description: 'Maximum number of groups this user can create',
		},
		chatColor: hex,
		id: uuid,
		joined: {
			type: 'number',
			minimum: 0,
		},
		lastLogin: {
			type: 'number',
			minimum: 0,
		},
		ownerId: uuid,
		profilePicture: uuid,
		staff: {
			...boolean,
			description: 'Whether the user is a member of Lyku staff',
		},
		username,
		channelLimit: whole,
		botToken: string,
		specials: {
			type: 'array',
			items: special,
		},
	},
	required: ['username', 'id', 'chatColor', 'joined'],
} as const;
export const botStuff = {
	type: 'object',
	properties: {
		bot: true,
		ownerId: uuid,
		token: sessionId,
	},
	required: ['bot', 'ownerId'],
} as const;

export const humanStuff = {
	type: 'object',
	properties: {
		bot: false,
		email: {
			...email,
			description: 'Email used for password resets',
		},
		gameStats,
		live: {
			type: 'boolean',
		},
		passhash: {
			type: 'string',
		},
	},
	required: [
		'chatColor',
		'email',
		'gameStats',
		'id',
		'joined',
		'live',
		'lastLogin',
		'passhash',
		'username',
	],
} as const;

export const bot = {
	type: 'object',
	properties: {
		...baseUser.properties,
		...botStuff.properties,
	},
	required: [...baseUser.required, ...botStuff.required],
} as const;

export const human = {
	type: 'object',
	properties: {
		...baseUser.properties,
		...humanStuff.properties,
	},
	required: [...baseUser.required, ...humanStuff.required],
} as const;

export const user = {
	type: 'object',
	properties: {
		...baseUser.properties,
		...botStuff.properties,
		...humanStuff.properties,
	},
	required: baseUser.required,
} as const;
