import { boolean, number, string } from '../../primitives';
import { uid } from '../../uid';

const date = {
	type: 'date',
} as const;
const meta = {
	type: 'object',
	properties: {
		name: 'string',
	},
	required: ['name'],
} as const;

const recording = {
	type: 'object',
	properties: {
		mode: string,
		requireSignedURLs: boolean,
		timeoutSeconds: number,
	},
	required: ['mode', 'requireSignedURLs', 'timeoutSeconds'],
} as const;

const rtmps = {
	type: 'object',
	properties: {
		streamKey: string,
		url: string,
	},
	required: ['streamKey', 'url'],
} as const;

const srt = {
	type: 'object',
	properties: {
		passphrase: string,
		streamId: string,
		url: string,
	},
	required: ['passphrase', 'streamId', 'url'],
} as const;

const webRTC = {
	type: 'object',
	properties: {
		url: string,
	},
	required: ['url'],
} as const;

const cfCreateStreamResult = {
	type: 'object',
	properties: {
		created: date,
		meta,
		modified: date,
		recording,
		rtmps,
		rtmpsPlayback: rtmps,
		srt,
		srtPlayback: srt,
		status: {
			type: 'null',
		},
		uid,
		webRTC,
		webRTCPlayback: webRTC,
	},
} as const;

export const createStream = {
	request: {},
	response: {
		type: 'object',
		properties: {
			errors: {
				type: 'array',
				items: string,
			},
			messages: {
				type: 'array',
				items: string,
			},
			result: cfCreateStreamResult,
			success: boolean,
		},
		required: ['errors', 'messages', 'result', 'success'],
	},
} as const;
