import { bio } from './bio';
import { channelName } from './channelName';
import { channelSlug } from './channelSlug';
import { hex } from './hex';
import { string } from './primitives';
import { tagline } from './tagline';
import { uuid } from './uuid';

export const cf = {
	type: 'object',
	properties: {
		// Live Input ID - system only - NOT visible
		inputId: string,
		// RTMPS Key - visible ONLY to owning user
		rtmpsKey: string,
		// SRT URL - visible ONLY to owning user
		srtUrl: string,
		// WebRTC (WHIP) URL - visible ONLY to owning user
		whipUrl: string,
		// Derived from WebRTC (WHEP) Playback URL
		// - include with channel summary for stream player
		whepKey: string,
	},
	required: ['inputId', 'rtmpsKey', 'srtUrl', 'whipUrl', 'whepKey'],
} as const;

export const channel = {
	type: 'object',
	properties: {
		live: {
			type: 'boolean',
		},
		fgColor: hex,
		bgColor: hex,
		tvColor: hex,
		id: uuid,
		owner: uuid,
		logo: string,
		created: {},
		cf,
		name: channelName,
		slug: channelSlug,
		tagline,
		bio,
		activeBg: string,
		awayBg: string,
	},
	required: ['id', 'name', 'slug', 'owner', 'live', 'input', 'created'],
} as const;
