import { FromSchema } from 'helpers';

import { monolith } from 'schema-constants';
import { sanitizedUser } from 'schema-constants';
import { SanitizedUser } from 'types';

import { useContract } from '../Contract';
import { getUserId } from '../getUserId';
import { sanitizeUserRecord } from '../sanitizeUserRecord';

export const listMyBots = useContract(
	'listMyBots',
	async (_, { tables, connection }, msg) => {
		const userId = await getUserId(msg, tables, connection);
		// if (typeof userId !== 'string') throw userId.error;
		const bots = await tables.Users.getAll(userId, { index: 'ownerId' })
			.map<FromSchema<typeof sanitizedUser>>(sanitizeUserRecord)
			.coerceTo('array')
			.run(connection);
		console.log('bots', bots);
		return { bots };
	}
);
