import { Document, now } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { Bot } from 'types';

import { useContract } from '../Contract';
import { Insertable } from '../Insertable';
import { Route } from '../Route';
import { generateSessionId } from '../generateSessionId';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

export const createBot = useContract(
	'createBot',
	async ({ username }, { tables, connection }, msg, strings) => {
		console.log('Creating bot');
		const userId = await getUserId(msg, tables, connection);
		const lowerUsername = username.toLocaleLowerCase();
		console.log('Checking for', lowerUsername);
		const existing = await tables.Users.filter((doc) =>
			doc('user').downcase().eq(lowerUsername)
		)
			.count()
			.gt(0)
			.run(connection);
		if (existing) throw strings.emailTaken;
		const token = generateSessionId();
		const bot: Document<Bot> = {
			bot: true,
			username,
			chatColor: 'FFFFFF',
			token,
			lastLogin: now(),
			joined: now(),
			ownerId: userId,
		};
		console.log('Inserting bot', { ...bot });
		const { generated_keys } = await tables.Users.insert(bot).run(
			connection
		);
		console.log('WAT', generated_keys);
		if (!generated_keys.length)
			throw 'idk lol some database shit went haywire';
		const [botId] = generated_keys;
		return { botId };
	}
);
