import { useContract } from '../Contract';
import { getUserId } from '../getUserId';
import { sanitizeUserRecord } from '../sanitizeUserRecord';

export const getCurrentUser = useContract(
	'getCurrentUser',
	async (_, { tables, connection }, msg, strings) => {
		console.log('GETCURRENTUSER1');
		if (!tables) {
			console.error('Tables fucked');
			throw new Error(strings.unknownBackendError);
		}
		console.log('session', msg.headers.sessionId);
		const userId = await getUserId(msg, tables, connection);
		const query = tables.Users.get(userId).do(sanitizeUserRecord);
		const res = await query.run(connection);
		console.log('GETCURRENTUSER', res);
		return { user: res };
	}
);
