import { attachment } from './attachment';
import { attachmentType } from './attachmentType';
import { postBody } from './postBody';
import { postTitle } from './postTitle';
import { number, string } from './primitives';
import { uuid } from './uuid';

export const attachmentDescriptor = {
	type: 'object',
	properties: {
		type: attachmentType,
		details: attachment,
	},
} as const;

export const post = {
	description: 'A post containing text, images, or videos',
	type: 'object',
	properties: {
		id: uuid,
		body: postBody,
		bodyType: {
			enum: ['plaintext', 'markdown'],
		},
		echoes: number,
		groupId: uuid,
		attachments: {
			type: 'array',
			items: attachmentDescriptor,
		},
		authorId: uuid,
		likes: number,
		loves: number,
		published: string,
		replies: number,
		title: postTitle,
		thread: {
			type: 'array',
			items: uuid,
		},
		shortcode: string,
	},
	required: [
		'id',
		'authorId',
		'attachments',
		'published',
		'likes',
		'echoes',
		'replies',
	],
} as const;
