import { SanitizedUser, User } from 'types';

export const sanitizeUserRecord = (
	user: Row<User>
): Document<SanitizedUser> => ({
	username: user('username'),
	live: user('live').default(false),
	chatColor: user('chatColor'),
	id: user('id'),
	gameStats: user('gameStats').default(null),
	profilePicture: user('profilePicture').default(null),
	bot: user('bot').default(false),
	staff: user('staff').default(false),
});
