import addFormats from 'ajv-formats';
import { FromSchema } from 'helpers';
import { Contract } from 'helpers';
// import Ajv from 'ajv/dist/jtd';
import { IncomingMessage } from 'http';
import { ServerResponse } from 'http';
import { CompactedPhrasebook } from 'phrasebooks';

import { monolith as schemas } from 'schema-constants';
import { monolith } from 'types';

import { HardenedState } from './types/State';
import { ajvConfig, vaj } from './vaj';

// type Monolith = typeof monolith;
// type MonoContractName = keyof Monolith;
// type MonolithContract = Monolith[MonoContractName];

// const ajv = new Ajv(ajvConfig as any);
// addFormats(ajv);

export const useContract = <C extends keyof monolith>(
	contract: C,
	respond: (
		params: 'request' extends keyof monolith[C]
			? monolith[C]['request']
			: never,
		state: HardenedState,
		msg: IncomingMessage,
		strings: CompactedPhrasebook,
		response: ServerResponse<typeof msg> & { req: typeof msg }
	) => Promise<monolith[C]['response']>
) => ({
	validate:
		'request' in schemas[contract]
			? vaj.compile(schemas[contract].request)
			: () => true,
	respond,
	contract,
	authenticated:
		'authenticated' in schemas[contract]
			? schemas[contract]['authenticated']
			: false,
});
