import { bio } from '../../bio';
import { channelName } from '../../channelName';
import { hex } from '../../hex';
import { sanitizedChannel } from '../../sanitizedChannel';
import { tagline } from '../../tagline';
import { uuid } from '../../uuid';

const request = {
	type: 'object',
	properties: {
		fgColor: hex,
		bgColor: hex,
		tvColor: hex,
		id: uuid,
		owner: uuid,
		name: channelName,
		tagline,
		bio,
	},
	required: ['id'],
} as const;

const response = {
	type: 'object',
	properties: {
		channel: sanitizedChannel,
	},
	required: ['channel'],
} as const;
export const updateChannel = {
	request,
	response,
	authenticated: true,
} as const;
