// import io from '@pm2/io';
import {
	Server as HttpServer,
	IncomingMessage,
	RequestListener,
	ServerResponse,
} from 'http';
import { Server as HttpsServer } from 'https';
import { CompactedPhrasebook } from 'phrasebooks';
import { branch, connect, dbCreate, dbList, db as getDb } from 'rethinkdb';

import { monolith, uuid } from 'schema-constants';

import * as routes from './routes';
import { phrasebook } from './../../../../libs/ui-bits/src/phrasebook';
import { useContract } from './Contract';
import { apiSplash } from './apiSplash';
import { originCertificate, privateKey, servicePort, webuiDomain } from './env';
import { getDictionary } from './getDictionary';
import { getTables } from './getTables';
import { isSessionId } from './isSessionId';
import { authorizeImageUpload } from './routes';
import { dbName, reifyTables } from './types/ActualTables';
import { HardenedState, State } from './types/State';

// const currentReqs = io.counter({
// 	name: 'Realtime request count',
// 	id: 'app/realtime/requests',
// });
const uuidPattern = new RegExp(uuid.pattern);
const isUuid = (thing: unknown) =>
	typeof thing === 'string' && uuidPattern.test(thing);

const ender =
	(res: ServerResponse, phrasebook: CompactedPhrasebook) =>
	(thing: unknown, code = 200, headers?: Record<string, string>) => {
		if (headers)
			for (const [k, v] of Object.entries(headers)) res.setHeader(k, v);
		res.statusCode = code;
		res.statusMessage = code in phrasebook ? phrasebook[code] : '';
		res.end(typeof thing === 'string' ? thing : JSON.stringify(thing));
	};
const addBasicHeaders = (res: ServerResponse, secure: boolean = false) => {
	const allowed = `${secure ? 'https' : 'http'}://${webuiDomain}`;
	console.log('allowed', allowed);
	res.setHeader('Access-Control-Allow-Origin', allowed);
	res.setHeader('Content-Type', 'application/json; charset=utf-8');
	res.setHeader('Access-Control-Allow-Headers', 'sessionid, Set-Cookie');
	res.setHeader('Access-Control-Allow-Credentials', 'true');
};
export class Server {
	state: State = {};
	setState = (fuck: State) => Object.assign(this.state, fuck);
	httpServer?: HttpServer | HttpsServer;
	init = async () => {
		console.log('Connecting generically');
		let c = await connect({});
		console.log('Reifying database');
		await branch(dbList().contains(dbName), null, dbCreate(dbName)).run(c);
		console.log('Connecting to', dbName);
		c = await connect({ db: dbName });
		const db = getDb(dbName);
		await reifyTables(db, c);
		console.log('Creating Http server');
		this.setState({
			db,
			tables: getTables(db),
			connection: c,
			isSecure: Boolean(originCertificate && privateKey),
		});
		console.log('HTTPS?', this.state.isSecure);
		this.httpServer = this.state.isSecure
			? new HttpsServer(
					{
						cert: originCertificate,
						key: privateKey,
					},
					this.requestListener
			  )
			: new HttpServer(this.requestListener);
		this.httpServer.listen(servicePort);
		console.log('Http server listening on port', servicePort);
	};
	requestListener: RequestListener = (msg, res) => {
		// currentReqs.inc();
		console.log('Received new request');

		const phrasebook = getDictionary(msg);

		addBasicHeaders(res, this.state.isSecure);
		const incoming: string[] = [];
		const end = ender(res, phrasebook);

		if (msg.method === 'GET')
			return end(apiSplash, 200, {
				'Content-Type': 'text/html; charset=utf-8',
			});

		if (!msg.url) return end('No route', 404);
		const path = msg.url.substring(1);
		console.log('Path', path);
		if (!(path in routes)) return end(phrasebook.invalidRoute, 404);
		console.log(
			path,
			'is in apis, sessionid',
			msg.headers.sessionid,
			'all',
			msg.headers
		);
		const hasBotHeaders = (msg: IncomingMessage) =>
			isUuid(msg.headers.botid) && isSessionId(msg.headers.token);
		msg.on('data', (d) => {
			console.log(path, 'pushing incoming', d);
			incoming.push(d);
		});
		msg.on('end', () => {
			console.log('Data received');
			const joined = incoming.join('');

			const { validate, respond, contract, authenticated } =
				routes[path as keyof typeof routes];
			if (
				authenticated &&
				!(isSessionId(msg.headers.sessionid) || hasBotHeaders(msg))
			)
				return end(phrasebook.notLoggedIn, 401);
			if (Object.values(this.state).length < 4) {
				console.error('Bad State of affairs');
				return end(phrasebook.unknownBackendError, 500);
			}
			console.log(path, 'joined', joined);
			console.log(
				path,
				'schema',
				'schema' in validate ? validate.schema : '[none]'
			);
			const parsed = JSON.parse(joined || '{}');
			const valid = validate(parsed);
			console.log(path, 'VALID?  ', valid);
			if (!valid) return end('lol nope', 400);
			const params = parsed;
			if (!params) return end(phrasebook.invalidRequestJson, 404);
			if (!this.state.connection?.open)
				return end(phrasebook.unknownBackendError, 500);
			console.log('Responding');
			respond(
				params as never,
				this.state as HardenedState,
				msg,
				phrasebook,
				res
			)
				.then(end)
				.catch((e) => {
					console.error('Route threw', e);
					if (/^[0-9]{3}$/.test(e.message))
						return end(e.message, parseInt(e.message));
					end(e.message, 500);
				});
			console.log('ended');
		});
	};
	stop = async () => {
		this.httpServer?.close();
		await this.state.connection?.close();
	};
}
