import { desc } from 'rethinkdb';

import { monolith } from 'schema-constants';

import { useContract } from '../Contract';

export const listMessages = useContract(
	'listMessages',
	async (_, { tables, connection }) => {
		const messages = await tables.Messages.orderBy(desc('r_date'))
			.limit(20)
			.limit(1)
			.coerceTo('array')
			.run(connection);
		return { messages };
	}
);
