// type CompoundIndexOf<Ta extends MyDbConfig[keyof MyDbConfig]> = Ta[keyof Ta][];
export const tableNames = Object.keys(
	dbConfig.tables
) as (keyof typeof dbConfig.tables)[];
// const keyedTableNames = tableNames.filter(
// 	(name) => 'indexes' in dbConfig.tables[name] && dbConfig.tables[name].indexes?.length
// );
// export type Tables = {
// 	[key in (typeof tableNames)[number]]: Table;
// };
export type ActualTables = {
	[TableName in keyof MyDbConfig['tables']]: Table<
		FromSchema<MyDbConfig['tables'][TableName]['schema']>
	>;
};
const queryMissingTables = (db: Db) =>
	expr(tableNames).filter((name) => db.tableList().contains(name).not());
export const reifyTables = async (db: Db, connection: Connection) => {
	await queryMissingTables(db)
		.forEach((table) => db.tableCreate(table))
		.run(connection);
	// const tables = Object.fromEntries(keyedTableNames.map((name) => [name, db.table(name)]))
	const tables = tableNames.reduce(
		(o, t) => ({
			...o,
			[t]: db.table(t),
		}),
		{}
	) as ActualTables;
	const indexesCreated = await reifyIndexes(dbConfig, tables, connection);
	const filtered = indexesCreated.filter((v) => v);
	if (filtered.length) console.log('Created indexes', filtered);
};
type MyTableName = keyof MyDbConfig['tables'];
type MyTable = MyDbConfig['tables'][MyTableName];
// type MyTableIndex<Tab> = (Tab extends {indexes: string[]} ? Tab['indexes'] : [])['indexes']
const reifyIndexes = (
	{ tables: config }: MyDbConfig,
	tables: ActualTables,
	connection: Connection
) => {
	const tableKeyPairs = Object.entries(config)
		.filter(([, table]) => 'indexes' in table && table.indexes.length)
		.flatMap(([tableName, t]) =>
			'indexes' in t
				? (t as { indexes: (typeof t)['indexes'] }).indexes.map(
						(index) => ({ table: tableName as MyTableName, index })
				  )
				: []
		); // as {table: MyTableName, index: keyof }[];
	return Promise.all(
		tableKeyPairs.map(({ table, index }) => {
			const t = tables[table];
			return (
				typeof index === 'string'
					? branch(
							tables[table].indexList().contains(index),
							0,
							tables[table].indexCreate(index).do(() => index)
					  )
					: mapCompoundIndex(t, index)
			).run(connection);
		})
	);
};
type MyTables = typeof dbConfig.tables;
