// type Indexes<Table extends MyTable> = Table extends {indexes: (keyof Props<Table>)[]}
// 	? Table['indexes']
// 	: [];
// type IndexOf<Table extends MyTable> = Indexes<Table>[number];
// : Table['schema'] extends { enum: { type: 'object', properties: Record<string, any>[] } }
// 	? Table['schema']['enum']['properties'] : {};

const mapCompoundIndex = (
	table: ActualTables[keyof ActualTables],
	index: CompoundIndex
) => {
	const bond = Object.keys(index)[0];
	return branch(
		table.indexList().contains(bond),
		0,
		table
			.indexCreate(
				bond, // as keyof Props<MyTables[keyof MyTables]>,
				index[bond].map((key: string) => row(key))
			)
			.do(() => index)
	);
};
