		// 		const type = attachments[a];
		// 		if (type === 'video' || type === 'image') {
		// 			const res = await directUpload(userId, authRes.postId, type, strings);
		// 			if ('error' in res) return res;
		// 			atAts.push(res);
		// 		}
		// 	}
		// 	const dbres = await tables.AttachmentDrafts.insert(atAts).run(connection);
		// 	console.log('Attachment draft insertion results', dbres);
		// }
		console.log('post', insertablePost);
