import { Document, branch, now, uuid } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { Channel, Group, SanitizedChannel } from 'types';

import { useContract } from '../Contract';
import { CreateStreamResponse } from '../CreateStreamResponse';
import { Route } from '../Route';
import { cfAccountId, cfApiToken } from '../env';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

import phin = require('phin');

export const updateGroup = useContract(
	'updateGroup',
	async (params, { tables, connection }, msg, strings) => {
		if (!tables) {
			console.error('Tables missing');
			throw new Error(strings.unknownBackendError);
		}
		const userId = await getUserId(msg, tables, connection);

		const group: Document<Group> = params;
		const groupExists = tables.Groups.get(params.id);
		const youCanEdit = groupExists('owner').eq(userId);
		const res = await groupExists
			.branch(
				youCanEdit.branch(groupExists.update(params), {
					error: 'That is your your toy to play with',
				}),
				{ error: 'Group not found, maybe make it?' }
			)
			.run(connection);
		if (!res) {
			console.error('idk wtf happened but res is undefined');
			throw new Error(strings.unknownBackendError);
		}
		if ('error' in res) {
			throw new Error(res.error);
		}

		return { group };
	}
);
