import { Document, branch, now, uuid } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { Channel, SanitizedChannel } from 'types';

import { useContract } from '../Contract';
import { CreateStreamResponse } from '../CreateStreamResponse';
import { Route } from '../Route';
import { cfAccountId, cfApiToken } from '../env';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

import phin = require('phin');

export const createChannel = useContract(
	'createChannel',
	async ({ name }, { tables, connection }, msg, strings) => {
		if (!tables) {
			console.error('Tables missing');
			throw new Error(strings.unknownBackendError);
		}
		const userId = await getUserId(msg, tables, connection);
		const user = tables.Users.get(userId);
		const channels = tables.Channels.getAll(userId, {
			index: 'owner',
		});
		const underLimit = channels.count().lt(user('channelLimit').default(2));
		const channelExists = tables.Channels.getAll(name, {
			index: 'name',
		})
			.limit(1)
			.count()
			.gt(0);
		const query = branch(
			channelExists,
			{ error: strings.channelAlreadyExists },
			branch(
				underLimit,
				{ uuid: uuid() },
				{ error: strings.channelLimitReached }
			)
		);
		const res = await query.run(connection);
		if (!res) {
			console.error('idk wtf happened but res is undefined');
			throw new Error(strings.unknownBackendError);
		}
		if ('error' in res) {
			throw new Error(res.error);
		}
		console.log(
			'User exists and is below channel limit. Query returned',
			res
		);
		const { uuid: id } = res;
		const url = `https://api.cloudflare.com/client/v4/accounts/${cfAccountId}/stream/live_inputs`;

		const channel: Document<Channel> = {
			live: false,
			id,
			owner: userId,
			created: now(),
			name,
			slug: name.toLowerCase(),
		};
		const insertResult = await tables.Channels.insert(channel).run(
			connection
		);
		console.log('Inserted channel', id, 'in database', insertResult);
		const cfres = await phin({
			url,
			method: 'POST',
			headers: {
				Authorization: `Bearer ${cfApiToken}`,
			},
			data: {
				defaultCreator: id,
				recording: {
					mode: 'automatic',
					allowedOrigins: [
						'lyku.org',
						'www.lyku.org',
						'bouncing.tv',
						'www.bouncing.tv',
					],
				},
			},
		});
		const json = JSON.parse(cfres.body.toString()) as CreateStreamResponse;
		console.log('Cloudflare stream creation response:', json);
		if (!json.success) throw new Error(strings.streamCreationError);
		const cf = {
			inputId: json.result.uid,
			rtmpsKey: json.result.rtmps.streamKey,
			srtUrl: json.result.srt.url,
			whipUrl: json.result.webRTC.url,
			whepKey: json.result.webRTCPlayback.url,
		};
		const channelUpdateResult = await tables.Channels.get(channel.id)
			.update({ cf })
			.run(connection);
		console.log('Inserted channel in database', channelUpdateResult);
		const sanitizedChannel = {
			live: channel.live,
			id: channel.id,
			owner: channel.owner,
			whepKey: channel.cf?.whepKey,
		} as SanitizedChannel;
		return { channel: sanitizedChannel };
	}
);
