import { and, now } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { Post } from 'types';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

export const likePost = useContract(
	'likePost',
	async (postId, { tables, connection }, msg) => {
		const userId = await getUserId(msg, tables, connection);
		// const post = tables.Posts.get(postId);
		const likeId = `${userId}~${postId}`;
		const postExistsAndLikeDoesnt = await and(
			tables.Posts.get(postId),
			tables.Likes.get(likeId).not()
		).run(connection);
		if (!postExistsAndLikeDoesnt)
			throw "Post doesn't exist or is already liked";
		console.log('Post exists and is not already liked, inserting like');
		await tables.Likes.insert({
			id: likeId,
			userId,
			postId,
			when: now(),
		}).run(connection);
		const post = await tables.Posts.get(postId)
			.update((p: Ex<Post>) => ({ likes: p('likes').add(1) }), {
				returnChanges: true,
			})('changes')(0)('new_val')
			.run(connection);
		console.log('YAY YOU PASS');
		return { post: { ...post, likedByYou: true } };
	}
);
