import { message } from '../../message';
import { uuid } from '../../uuid';

export const listMessages = {
	request: {
		type: 'object',
		properties: {
			channel: uuid,
		},
		required: ['channel'],
	},
	response: {
		type: 'object',
		properties: {
			messages: {
				type: 'array',
				items: message,
			},
		},
		required: ['messages'],
	},
	authenticated: true,
} as const;
