import { monolith } from 'schema-constants';
import { Channel, ChannelSensitives } from 'types';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { sensitizeChannelRecord } from '../sensitizeChannelRecord';
import { vaj } from '../vaj';

export const getChannelSensitives = useContract(
	'getChannelSensitives',
	async ({ channelId }, { tables, connection }, msg, strings) => {
		const userId = await getUserId(msg, tables, connection);
		const channelBy = tables.Channels.get(channelId).do((v: Ex<Channel>) =>
			v.and(v('owner').eq(userId)).branch(v.do(sensitizeChannelRecord), {
				error: strings.youHaveNoChannelByThatId,
			})
		);
		const res = await channelBy.run(connection);
		if ('error' in res) {
			throw new Error(res.error);
		}
		console.log('channel', res);
		return { sensitives: res };
	}
);
