import { FromSchema } from 'helpers';
import { and, branch, desc, expr, or, row } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { post } from 'schema-constants';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { ActualTables } from '../types/ActualTables';
import { vaj } from '../vaj';

// const hasGroupId = row.hasFields('groupId');
// const hasntGroupId = hasGroupId.not();
type FilterMap = Partial<
	Record<
		string,
		(
			userIdQuery: Ex<string> | string,
			tables: ActualTables
		) => (row: Ex<FromSchema<typeof post>>) => Ex<boolean>
	>
>;
const filterMap: FilterMap = {
	users: (userId, tables) => (row) =>
		and(
			row.hasFields('groupId').not(),
			or(
				row('authorId').eq(userId),
				tables.UserFollows.getAll([userId, row('authorId')], {
					index: 'bond',
				})
			)
		),
	groups: (userId, tables) => (row) =>
		and(
			row.hasFields('groupId'),
			tables.GroupMemberships.getAll(
				[userId, row('groupId') as Ex<string>],
				{
					index: 'bond',
				}
			)
		),
	all: () => () => and(expr(true), expr(true)), // god help me
};

export const listFeedPosts = useContract(
	'listFeedPosts',
	async ({ before, tags, groups, filter }, { tables, connection }, msg) => {
		const userId = await getUserId(msg, tables, connection);
		// let query = tables.Posts.orderBy(desc('published')).eqJoin('authorId', tables.Users).map(row => ({post: row('left'), author: row('right')}));
		let query = tables.Posts.orderBy(desc('published')).merge(
			(p: Ex<FromSchema<typeof post>>) => ({
				author: tables.Users.get(p('authorId')),
				likedByYou: tables.Likes.getAll([userId, p('id')], {
					index: 'bond',
				})
					.limit(1)(0)('id')
					.default(false),
				// likes: tables.Likes.getAll(post('id'), {index: 'postId'}).count()
			})
		);
		const filters: (
			| Ex<boolean>
			| ((ro: Ex<FromSchema<typeof post>>) => Ex<boolean>)
		)[] = [];
		if (before) filters.push(row('postDate').lt(new Date(before)));
		if (filter && filter in filterMap)
			filters.push(filterMap[filter as keyof FilterMap]!(userId, tables));
		if (groups)
			filters.push((ro: Ex<FromSchema<typeof post>>) =>
				and(
					ro.hasFields('groupId'),
					(groups === true
						? tables.GroupMemberships.getAll(userId, {
								index: 'userId',
						  })
						: expr(groups)
					).includes(row('groupId'))
				)
			);
		if (tags)
			filters.push((ro: Ex<FromSchema<typeof post>>) =>
				and(
					ro.hasFields('lowerTags'),
					row('lowerTags')
						.filter((r: string) => expr(tags).contains(r))
						.limit(1)
				)
			);
		if (filters.length)
			query = query.filter(
				filters.length > 1 ? and(...filters) : filters[0]
			);
		query = query.merge((p: Ex<FromSchema<typeof post>>) => ({
			author: tables.Users.get(p('authorId')),
			likedByYou: tables.Likes.getAll([userId, p('id')], {
				index: 'bond',
			})
				.limit(1)(0)('id')
				.default(false),
			// likes: tables.Likes.getAll(post('id'), {index: 'postId'}).count()
		}));
		query = query.limit(20).coerceTo('array');
		const res = await query.run(connection);
		console.log('Listing', res.length, 'posts');
		return { posts: res };
	}
);
