import { branch } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { SanitizedChannel } from 'types';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { sanitizeChannelRecord } from '../sanitizeChannelRecord';
import { vaj } from '../vaj';

export const listMyChannels = useContract(
	'listMyChannels',
	async (_, { tables, connection }, msg, strings) => {
		const userId = await getUserId(msg, tables, connection);
		const channels = tables.Channels.getAll(userId, {
			index: 'owner',
		})
			.map<SanitizedChannel>(sanitizeChannelRecord)
			.coerceTo('array');
		const res = await channels.run(connection);
		if (!res) {
			console.error('idk wtf happened but res is undefined');
			throw new Error(strings.unknownBackendError);
		}
		if ('error' in res) throw new Error(res.error?.toString());
		console.log('channels', channels);
		return { channels: res };
	}
);
