import { FromSchema } from 'helpers';
import { and, desc, expr, row } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { post } from 'schema-constants';

import { useContract } from '../Contract';

// const hasGroupId = row.hasFields('groupId');
// const hasntGroupId = hasGroupId.not();

export const listFeedPostsUnauthenticated = useContract(
	'listFeedPostsUnauthenticated',
	async ({ before, groups, tags }, { tables, connection }) => {
		// let query = tables.Posts.orderBy(desc('published')).eqJoin('authorId', tables.Users).map(row => ({post: row('left'), author: row('right')}));
		let query = tables.Posts.orderBy(desc('published')).merge(
			(p: Ex<FromSchema<typeof post>>) => ({
				author: tables.Users.get(p('authorId')),
				// likes: tables.Likes.getAll(post('id'), {index: 'postId'}).count()
			})
		);
		const filters: ReturnType<typeof and>[] = [];
		if (before) filters.push(row('postDate').lt(new Date(before)));
		if (groups)
			filters.push(
				and(
					row.hasFields('groupId'),
					expr(groups).includes(row('groupId'))
				)
			);
		if (tags)
			filters.push(
				and(
					row.hasFields('lowerTags'),
					row('lowerTags')
						.filter((r: string[]) => expr(tags).contains(r))
						.limit(1)
				)
			);
		if (filters.length)
			query = query.filter(
				filters.length > 1 ? and(...filters) : filters[0]
			);
		const res = await query.run(connection);
		if ('error' in res) throw { error: res.error };
		console.log('Listing posts', res);
		return { posts: res };
	}
);
