import { postWithExtras } from '../../postWithExtras';
import { number } from '../../primitives';
import { uuid } from '../../uuid';

const listFeedFilter = {
	enum: ['users', 'groups', 'all'],
} as const;
export const listFeedPosts = {
	request: {
		type: 'object',
		properties: {
			groups: {
				enum: [
					{
						type: 'array',
						items: uuid,
					},
					true,
				],
			},
			tags: {
				type: 'array',
				items: uuid,
			},
			filter: listFeedFilter,
			before: number,
		},
		required: [],
	},
	response: {
		type: 'object',
		properties: {
			posts: {
				type: 'array',
				items: postWithExtras,
			},
			// authors: {
			//     type: 'object',
			//     patternProperties: {
			//         [uuid.pattern]: sanitizedUser
			//     }
			// },
		},
		required: ['posts'],
	},
	authenticated: true,
} as const;
