import * as bcrypt from 'bcryptjs';
import { now, or } from 'rethinkdb';

import { monolith } from 'schema-constants';
import { User } from 'types';

import { useContract } from '../Contract';
import { createSessionForUser } from '../createSessionForUser';
import { newGameSession } from '../newGameSession';

export const registerUser = useContract(
	'registerUser',
	async (
		{ email, username, password },
		{ tables, connection },
		msg,
		strings,
		res
	) => {
		const lowerEmail = email.toLocaleLowerCase();
		const lowerUsername = username.toLocaleLowerCase();
		console.log('Checking for', lowerEmail, 'or', lowerUsername);
		const existing = await tables.Users.filter((doc) =>
			or(
				doc('email').eq(lowerEmail),
				doc('user').downcase().eq(lowerUsername)
			)
		)
			.count()
			.gt(0)
			.run(connection);
		if (existing) throw new Error(strings.emailTaken);
		console.log('Hashing password');
		const passhash = await bcrypt.hash(password, 10);
		const user = {
			username,
			passhash,
			email,
			chatColor: 'FFFFFF',
			live: false,
			gameStats: {
				current: newGameSession(),
				total: newGameSession(),
				highest: newGameSession(),
				sessionCount: 0,
			},
			lastLogin: now(),
			joined: now(),
		};
		console.log('Inserting user', { ...user, passhash: 'redacted' });
		const { generated_keys } = await tables.Users.insert(user).run(
			connection
		);
		if (!generated_keys.length)
			throw new Error(strings.unknownBackendError);
		const [userId] = generated_keys;
		const sessionId = await createSessionForUser(
			userId,
			msg,
			tables,
			connection
		);
		res.setHeader('Set-Cookie', `sessionid=${sessionId}; Path=/;`);
		console.log('Logged user in', sessionId);
		return { sessionId };
	}
);
