import { monolith } from 'schema-constants';
import { Channel } from 'types';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { sanitizeChannelRecord } from '../sanitizeChannelRecord';
import { vaj } from '../vaj';

export const getMyChannel = useContract(
	'getMyChannel',
	async ({ name, id }, { tables, connection }, msg, strings) => {
		const userId = await getUserId(msg, tables, connection);
		let channelBy;
		if (id)
			channelBy = tables.Channels.get(id).do((v: Ex<Channel>) =>
				v
					.and(v('owner').eq(userId))
					.branch(v.do(sanitizeChannelRecord), {
						error: strings.youHaveNoChannelByThatId,
					})
			);
		else if (name)
			channelBy = tables.Channels.getAll(name, {
				index: 'name',
			})
				.filter({ owner: userId })(0)
				.do(sanitizeChannelRecord)
				.default({
					error: strings.youHaveNoChannelByThatName,
				});
		else throw new Error(strings.invalidRequestParams);
		const query = channelBy;
		const res = await query.run(connection);
		if (!res) {
			console.error(
				'Yo what the fuck happened, the db returned empty but it should error'
			);
			throw new Error(strings.unknownBackendError);
		}
		if ('error' in res) {
			throw new Error(res.error);
		}
		console.log('channel', res);
		return { channel: res };
	}
);
