import { monolith } from 'schema-constants';
import { Post } from 'types';

import { useContract } from '../Contract';
import { Route } from '../Route';
import { getUserId } from '../getUserId';
import { vaj } from '../vaj';

export const getPost = useContract(
	'getPost',
	async ({ id }, { tables, connection }, msg) => {
		console.log('Getting post', id);
		const userId = await getUserId(msg, tables, connection).catch(
			() => undefined
		);
		const liked = userId
			? (post: Ex<Post>) => ({
					likedByYou: tables.Likes.getAll([userId, post('id')], {
						index: 'bond',
					})
						.limit(1)(0)('id')
						.default(false),
			  })
			: () => ({});
		const post = await tables.Posts.get(id)
			.merge((post: Ex<Post>) => ({
				author: tables.Users.get(post('authorId')),
				...liked(post),
			}))
			.run(connection);
		console.log('Returning post', post);
		return { post };
	}
);
