import { monolith } from 'schema-constants';
import { Channel } from 'types';

import { useContract } from '../Contract';
import { sanitizeChannelObject } from '../sanitizeChannelObject';

export const updateChannel = useContract(
	'updateChannel',

	async (
		{ id, name, tagline, bio, bgColor, fgColor, tvColor },
		{ tables, connection },
		msg,
		strings
	) => {
		const session = tables.Sessions.get(msg.headers.sessionid as string);
		const userId = session('userId');
		const partial = Object.fromEntries(
			Object.entries({
				name,
				tagline,
				bio,
				bgColor,
				fgColor,
				tvColor,
			}).filter(([, v]) => typeof v !== 'undefined')
		);
		console.log('Partial channel update', partial);
		const channel = tables.Channels.get(id);
		const updateChannel = channel
			.and(channel('owner').eq(userId))
			.branch(channel.update(partial, { returnChanges: true }), null)
			.default(null);
		const query = session.branch(updateChannel, {
			error: strings.sessionNotFound,
		});
		const res:
			| { error: string }
			| {
					replaced: number;
					changes: { old_val: Channel; new_val: Channel }[];
			  }
			| null = await query.run(connection);
		if (!res) {
			throw new Error(strings.youHaveNoChannelByThatId);
		}
		if ('error' in res) {
			throw new Error(res.error);
		}
		if (res.replaced !== 1 || res.changes.length !== 1)
			throw new Error(strings.unknownBackendError);
		// console.log('channel', res);
		const sanitized = sanitizeChannelObject(res.changes[0].new_val);
		return { channel: sanitized };
	}
);
